import React from "react";
import "./Square.css";

export default class Square extends React.Component {

  render() {
    return (
      <button
        className="square"
        onClick={this.props.onClick}
        style={{
          background: this.props.highlighted ? "white" : undefined
        }}
      >
        {this.props.value}
      </button>
    );
  }
}