import React from "react";
import './TopNavigation.css';
import {Link} from "react-router-dom";

export default class TopNavigation extends React.Component {

  render() {
    return (
      <div className="topNavigation">
        <ul>
          <li>
            <Link to="home">
              <button>Home</button>
            </Link>
          </li>
          <li>
            <Link to="game">
              <button>Game</button>
            </Link>
          </li>
        </ul>
      </div>
    );
  }
}